<script>
    import ToDoInput from './ToDoInput.svelte'
    import ToDoList from './ToDoList.svelte'
    import {v4 as uuid} from 'uuid'
    import { onMount } from 'svelte';
    import pdate from 'any-date-parser';

    let data = {};

    function saveData() {
        data.todos = data.todos;
        localStorage.setItem('data', JSON.stringify(data));
    }

    function completeToDo(e) {
        let id = e.detail.id;
        delete data.todos[id];

        saveData();
    }

    function addToDo(e) {
        let text = e.detail.text;
        let id = uuid();
        let deadline = pdate.fromString('in 8h');

        data.todos[id] = { text, deadline, id };

        saveData();
    }

    function createPlaceholderToDos() {
        console.log('Creating placeholder items..');
        data = {};
        data.todos = {};

        addToDo({detail: {text: 'Do something.'}});
        addToDo({detail: {text: 'And now do something else.'}});

        saveData();
    }

    onMount(() => {
        data = JSON.parse(window.localStorage.getItem('data'));
        if(data == null || Object.keys(data.todos).length == 0) createPlaceholderToDos()

        //createPlaceholderToDos();
        
        console.log(data);
    });
</script>

<div id='outer'>
    <ToDoInput on:add={addToDo}/>
    <ToDoList todos={data.todos} on:complete={completeToDo}/>
</div>

<style>
    #outer {
        display:flex;
        flex-direction: column;
        overflow:hidden;
        margin:0.5em;

    }

    @media (min-width: 768px) {
        #outer {
            margin:4em;
            width: 800px;
        }
    }
</style>