<script>
	import { createEventDispatcher } from 'svelte';

    const dispatch = createEventDispatcher();
    let inputBox = null;

    function handleKeyDown(e) {
        if(e.key == 'Enter') {
            e.preventDefault();
            e.stopPropagation();
            dispatch('add', {
                text: inputBox.value
            });
            inputBox.value = '';
        }
    }
</script>
<input bind:this={inputBox} type='text' placeholder='Describe your task' on:keydown={handleKeyDown}/>

<style>
    input {
        padding: 1em;
        border: solid 1px rgba(0, 0, 0, 0.25);
        font-size: 1em;
        background-color: rgb(29, 29, 29);
        color:white;
        text-shadow: 1px 1px rgba(0, 0, 0, 0.5);

    }

    input::placeholder {
        color: rgba(255, 255, 255, 0.2);
    }

    input:focus {     
        outline: none;
        border: solid 1px rgba(0, 0, 0, 0.75);
    }
</style>