<script>
    import ToDoItem from './ToDoItem.svelte'
    export let todos;

    $: {
        if(todos) { filteredList = Object.values(todos);} 
    }

    let filteredList = [];
</script>


<div id='todos'>
    {#each filteredList as todo (todo.id)}
        <ToDoItem item={todo} on:complete/>
    {/each}

</div>

<style>
    #todos {
        border: solid 1px rgba(0, 0, 0, 0.2);
        display: flex;
        flex-direction: column;
        overflow-y:auto;
        padding:1em;
    }
</style>
