<script>
    import { onMount } from "svelte";
    import { createEventDispatcher } from 'svelte';
    import pdate from 'any-date-parser';

    const dispatch = createEventDispatcher();
    let inputBox;
    let bgColor = 'rgb(29, 29, 29)'
    export let position;
    
    function hide(e) {
        e.preventDefault();
        e.stopPropagation();
        dispatch('hide');
    }
    
    function onInputClicked(e) {
        e.stopPropagation();
    }
    
    function parseDate(s) {
        let newDate = pdate.fromString(s);
        if(newDate.invalid) {
            return null;
        } else {
            return newDate;
        }
    }

    function onKeyUp(e) {
        e.preventDefault();
        if(e.key == 'Enter'){
            let newDate = parseDate(inputBox.value);
            if(newDate) {
                dispatch('updateDeadline', newDate);
                inputBox.value = null;
            } else {
                bgColor = 'rgb(255,0,0,0.5)';
            }
        }
    }

    onMount(() => {
        inputBox.style.top = position.Y + "px";
        inputBox.style.left = position.X + "px";
        inputBox.focus();
    });
</script>
<div on:click={hide}>
    <input style='background-color:{bgColor}' bind:this={inputBox} on:keyup={onKeyUp} on:click={onInputClicked} type='text' placeholder='When?'/>
</div>

<style>
    div {
        position:absolute;
        left:0;
        top:0;
        width:100%;
        height:100%;

        background-color: rgba(0, 0, 0, 0.2);
        z-index:1;


        display:flex;
        justify-content: center;
    }

    input {
        position:absolute;
        padding: 0.85em;
        border: solid 1px rgba(0, 0, 0, 0.25);
        font-size: 1em;
        color:white;
        text-shadow: 1px 1px rgba(0, 0, 0, 0.5);  
    }




    input::placeholder {
        color: rgba(255, 255, 255, 0.2);
    }

    input:focus {     
        outline: none;
        border: solid 1px rgba(0, 0, 0, 0.75);
    }

</style>

