<script>
    import { afterUpdate, onMount } from "svelte";
    import ToDoDeadlineSelector from "./ToDoDeadlineSelector.svelte";
    import { createEventDispatcher } from 'svelte';

    import { tweened } from "svelte/motion";
  import { quartInOut } from "svelte/easing";

    const dispatch = createEventDispatcher();
    export let item;
    let el;
    let active = false;
    let clickPos;

    let dateColor = 'rgb(255,255,255,0.25)';

    function boldHalf(word) {
        let half = Math.max(1,Math.round(word.length * 0.5));
        let boldedPart = word.substring(0, half);
        let otherPart = word.substring(half);
        return '<span class="bolded">' + boldedPart + '</span>' + '<span class="unbolded">' + otherPart + '</span>';
    }

    function processText() {
        if(!active) {
            let processed = '';
            let split = item.text.split(' ');
            
            for(let i = 0; i < split.length; i++) {
                processed += boldHalf(split[i]) + ' ';
            }
            if(el){
                el.innerHTML = processed;
            }
        }
    }

    function complete() {
        dispatch('complete', { id: item.id });
    }

    function toggleActive(e) {
        console.log(el);

        clickPos = {  X: e.clientX, Y: e.clientY };
        active = !active;
    }

    let interval = null;
    function onUpdateDeadline(e) {
        console.log(e.detail);
        active = false;
        let newDate = new Date(e.detail);
        item.deadline = newDate.getTime();

        dateColor = 'rgb(0,255,0,1.0)';

        if(interval)   clearInterval(interval);
        interval = setInterval(() => {
            dateColor = 'rgb(255,255,255,0.25)';
        }, 2000);
    }


    onMount(() => {
        processText();
    });

    afterUpdate(() => {
        processText();
    });

</script>

<div id='outer' on:click={toggleActive}>
    {#if active}
        <ToDoDeadlineSelector position={clickPos} on:hide={toggleActive} on:updateDeadline={onUpdateDeadline}/>
    {/if}

    <div id='left'>
        <p bind:this={el}/>
        <p id='date' style='color:{dateColor}'>{new Date(item.deadline).toLocaleString()}</p>
    </div>
    <div id='right'>
        <button on:click={complete}>Complete</button>
    </div>

</div>

<style>
    p {
        font-size: 14px;
        cursor: pointer;
        text-shadow: 1px 1px rgba(0, 0, 0, 0.5);
    }

    #outer {
        cursor: pointer;
        display: flex;
    }

    #outer:hover {
        background-color: rgba(255, 255, 255, 0.05);
    }

    #date {
        font-size: 12px;
        padding:0;
        transition: color 0.25s;
    }

    #left {
        padding:0.5em;
    }

    #right {
        margin-left:auto;
        visibility: hidden;
        padding:0.5em;
    }

    button {
        cursor: pointer;
        padding:0.5em;
        background:transparent;
        color:white;
        text-shadow: 1px 1px rgba(0, 0, 0, 0.5);
        border-radius: 4px;
        border-color: rgba(255,255,255,0.1);
    }

    button:hover {
        background-color: rgb(214, 214, 214);
        color:black;
        text-shadow: none;
    }

    button:active {
        background-color: rgb(77, 77, 77);
        color:white;
    }

    #outer:hover #right {
        visibility:visible;
    }

</style>